.components-progress {
    width: 100%;
    height: 3px;
    background: #ddd;
    cursor: pointer;
}

.progress {
    width: 0%;
    height: 3px;
    -webkit-transition: all .1s linear;
    -o-transition: all .1s linear;
    transition: all .1s linear;
    -webkit-transform-origin: left center;
        -ms-transform-origin: left center;
            transform-origin: left center;
}




