.container-player {
  width: 700px;
  margin: auto;
  margin-top: 120px;
}
.container-player .caption {
  font-size: 16px;
  color: #2f9842;
}
.container-player .cover {
  width: 180px;
  height: 180px;
  margin-left: 20px;
}
.container-player .cover img {
  width: 180px;
  height: 180px;
  border-radius: 50%;
}
.container-player .volume-container {
  position: relative;
  left: 20px;
  top: -3px;
}
.container-player .volume-container .volume-wrapper {
  opacity: 0;
  -webkit-transition: opacity .5s linear;
  -o-transition: opacity .5s linear;
  transition: opacity .5s linear;
}
.container-player .volume-container:hover .volume-wrapper {
  opacity: 1;
}
.container-player .music-title {
  font-size: 25px;
  font-weight: 400;
  color: #030303;
  height: 36px;
  line-height: 36px;
}
.container-player .music-artist {
  font-size: 15px;
  font-weight: 400;
  color: #4a4a4a;
}
.container-player .left-time {
  font-size: 14px;
  color: #999;
  font-weight: 400;
  width: 40px;
}
.container-player .icon {
  cursor: pointer;
}
.container-player .ml20 {
  margin-left: 20px;
}
.container-player .mt35 {
  margin-top: 35px;
}
.container-player .volume-wrapper {
  width: 60px;
  display: inline-block;
}
.container-player .spin{
  -webkit-animation: play 20s linear infinite;
          animation: play 20s linear infinite;
}
.container-player .running{
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
.container-player .paused{
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
@-webkit-keyframes play{
  0%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100%{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}
@keyframes play{
  0%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100%{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}


